body {
  height: 100vh;
  position: relative;
}

.cat-info {
  margin: 30px;
  display: flex;
}

.loader {
  box-sizing: border-box;
  background: linear-gradient(#ff3d00 30px, #0000 0) 50% 0 / 2px 40px no-repeat;
  border-top: 5px solid #000;
  border-bottom: 5px solid #000;
  width: 50px;
  height: 80px;
  animation: 5s linear infinite spinx;
  display: inline-block;
  position: relative;
  top: 40%;
  left: 50%;
}

.loader.hidden {
  display: none;
}

.loader:before, .loader:after {
  content: "";
  background: #00000040 0 0 / 100% no-repeat;
  border-radius: 0 0 20px 20px;
  width: 40px;
  height: 35px;
  animation: 5s linear infinite lqt;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.loader:after {
  border-radius: 20px 20px 0 0;
  animation: 5s linear infinite lqb;
  top: auto;
  bottom: 0;
}

@keyframes lqt {
  0%, 100% {
    background-image: linear-gradient(#ff3d00 40px, #0000 0);
    background-position: 0 0;
  }

  50% {
    background-image: linear-gradient(#ff3d00 40px, #0000 0);
    background-position: 0 40px;
  }

  50.1% {
    background-image: linear-gradient(#ff3d00 40px, #0000 0);
    background-position: 0 -40px;
  }
}

@keyframes lqb {
  0% {
    background-image: linear-gradient(#ff3d00 40px, #0000 0);
    background-position: 0 40px;
  }

  100% {
    background-image: linear-gradient(#ff3d00 40px, #0000 0);
    background-position: 0 -40px;
  }
}

@keyframes spinx {
  0%, 49% {
    background-position: 50% 36px;
    transform: rotate(0);
  }

  51%, 98% {
    background-position: 50% 4px;
    transform: rotate(180deg);
  }

  100% {
    background-position: 50% 36px;
    transform: rotate(360deg);
  }
}

.cat-info.hidden {
  display: none;
}

.cat-info span {
  font-weight: bold;
}

.cat-info p:last-child {
  margin-top: 10px;
}

.cat-img {
  margin-right: 20px;
}

.ss-main {
  width: 50%;
  left: 50%;
  transform: translateX(-50%);
}
